import { connectFirebase } from './data/rest.js';
import renderApp from './framework/render';
import dataStore from './data/dataStore';

renderApp();

// –∫–æ–ª–ª–±—ç–∫ –º–æ–∂–Ω–æ –∑–∞–ø–∏—Ö–Ω—É—Ç—å –≤ rest, –∞ –º–æ–∂–µ—Ç –∏ –≤–æ–æ–±—â–µ —Å–¥–µ–ª–∞—Ç—å –¥–ª—è –§–ë –æ—Ç–¥–µ–ª—å–Ω—ã–π
connectFirebase(data => {
  dataStore.setUserData(data);

  // —ç—Ç–æ –Ω–µ–≤–µ—Ä–Ω–æ –¥–ª—è —Ä–∞–∑–ª–æ–≥–∏–Ω–∞!!!
  // –≤–æ–æ–±—â–µ —ç—Ç–æ –¥–æ–ª–∂–Ω–æ –ø–æ–∫–∞–∑—ã–≤–∞—Ç—å—Å—è –∫–æ–≥–¥–∞ —é–∑–µ—Ä –≤–æ—à–µ–ª, –∏–Ω–∞—á–µ –∂ –±—É–¥–µ—Ç —Ñ–æ—Ä–º–∞ –ª–æ–≥–∏–Ω–∞
  dataStore.userDataIsLoaded = true;
  renderApp();
});
